.toy-edit {


    & .toy-edit-modal {

        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        box-shadow: -6px 0px 14px rgb(0 0 0 / 20%);
        margin: auto;
        border-radius: 0.5rem;
        border: lightgray solid 2px;
        width: 30vw;
        background-color: $clr2bg;
        padding: 30px 0;
        min-height: fit-content;
    }

    & .add-save-btns {
        margin: 20px;

        & button {
            margin: 5px;
            font-weight: 700;
        }
    }

    & form {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }


    & .edit-input {
        font-family: product-sans-regular, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

        padding: 5px 10px;
        border-radius: 0.5rem;
        border: lightgray solid 1px;

        &:focus {
            background-color: $clr1bg;
            color: $clr3;
        }

        &::placeholder {
            color: $clr3;
        }

    }
}

.toy-add {
    margin: 10px;

    & a {
        color: $gray1;
        font-weight: 800;
        margin: 0;
    }


}

.multi-select {
    position: relative;

    & .selected-options-container {
        display: flex;
        gap: 5px;
    }

    & .options-container {
        display: none;
        position: absolute;
        // top: 20px;
        // left: 20px;

        &.open {
            display: unset;
        }

    }

    & .options-container>* {
        padding: 5px;
        border: 1px solid lightgray;
        // border-radius: 0.5rem;
        background: white;
    }


}